import React, { useEffect, useState, useMemo } from "react";
import Pagination from "react-bootstrap/Pagination";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Spinner from "react-bootstrap/Spinner";
import Modal from "react-bootstrap/Modal";
import { useAuth } from "../contexts/AuthContext";
import { toast } from "react-toastify";

const GROUP_CODE_FIELDS = [
  { key: "id", label: "ID" },
  { key: "lineCode", label: "Line Code" },
  { key: "line", label: "Line" },
  { key: "firstNumber", label: "First Number" },
  { key: "lastNumber", label: "Last Number" },
  { key: "opsCode", label: "Ops Code" },
  { key: "inlineLogo", label: "Inline Logo" },
  { key: "bulletLogo", label: "Bullet Logo" },
  { key: "backLogo", label: "Back Logo" },
  { key: "gateLogo", label: "Gate Logo" },
  { key: "groupCode", label: "Group Code" },
  { key: "defaultLanguage", label: "Default Language" },
  { key: "siteId", label: "Site ID" },
];

function compare(a, b, key, direction) {
  if (a[key] == null && b[key] == null) return 0;
  if (a[key] == null) return 1;
  if (b[key] == null) return -1;
  if (typeof a[key] === "string") {
    return direction === "asc"
      ? a[key].localeCompare(b[key])
      : b[key].localeCompare(a[key]);
  }
  return direction === "asc" ? a[key] - b[key] : b[key] - a[key];
}

const GroupCodeCard = ({ group, onSave }) => {
  const [show, setShow] = useState(false);
  return (
    <>
      <Card
        className="mb-3 shadow-sm group-card-mini text-truncate"
        style={{ cursor: "pointer", minWidth: 0, maxWidth: 260, width: "100%" }}
        onClick={() => setShow(true)}
      >
        <Card.Body className="p-2 d-flex flex-column align-items-start justify-content-between">
          <div className="mb-1 text-truncate" style={{ width: "100%" }}><strong>ID:</strong> {group.id}</div>
          <div className="mb-1 text-truncate" style={{ width: "100%" }}><strong>Code:</strong> {group.groupCode}</div>
          <div className="mb-2 text-truncate" style={{ width: "100%", fontSize: "0.85em", color: "#888" }}><strong>Line:</strong> {group.line}</div>
          <Button variant="outline-primary" size="sm" className="align-self-end" onClick={e => { e.stopPropagation(); setShow(true); }}>Details</Button>
        </Card.Body>
      </Card>
      <GroupCodeDetailsModal show={show} onHide={() => setShow(false)} group={group} onSave={onSave} />
    </>
  );
};


function GroupCodeDetailsModal({ show, onHide, group, onSave, mode = "edit" }) {
  const [form, setForm] = useState(group || {});
  const [saving, setSaving] = useState(false);
  const [validationError, setValidationError] = useState("");
  const { authenticatedFetch } = useAuth();

  useEffect(() => {
    if (mode === "add") {
      // Reset form for add mode, set defaults for new group code
      setForm({
        id: 0,
        siteId: 1,
        firstNumber: 1,
        lastNumber: 9999,
        defaultLanguage: "en"
      });
    } else {
      setForm(group || {});
    }
    setValidationError("");
  }, [group, show, mode]);

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    setForm((f) => ({
      ...f,
      [name]: type === "checkbox" ? checked : value,
    }));
    setValidationError("");
  };

  const handleSave = async () => {
    // Validate required fields
    if (!form.lineCode || !form.line || !form.groupCode || form.firstNumber === undefined || form.firstNumber === "" || form.lastNumber === undefined || form.lastNumber === "") {
      setValidationError("Line Code, Line, Group Code, First Number, and Last Number are required.");
      return;
    }
    // Validate firstNumber and lastNumber are numbers between 1 and 9999
    const firstNum = Number(form.firstNumber);
    const lastNum = Number(form.lastNumber);
    if (
      isNaN(firstNum) || isNaN(lastNum) ||
      firstNum < 1 || firstNum > 9999 ||
      lastNum < 1 || lastNum > 9999
    ) {
      setValidationError("First Number and Last Number must be numbers between 1 and 9999.");
      return;
    }
    if (firstNum > lastNum) {
      setValidationError("First Number must be less than or equal to Last Number.");
      return;
    }
    setSaving(true);
    try {
      const baseUrl = import.meta.env.VITE_API_BASE_URL || "";
      let resp, updated;
      if (mode === "add") {
        // POST to create new group code
        resp = await authenticatedFetch(`${baseUrl}/api/GroupCodes`, {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify(form),
        });
        if (!resp.ok) throw new Error("Failed to create group code");
        updated = await resp.json();
        toast.success("Group code created");
      } else {
        // PUT to update existing group code
        resp = await authenticatedFetch(`${baseUrl}/api/GroupCodes/${form.id}`, {
          method: "PUT",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify(form),
        });
        if (!resp.ok) throw new Error("Failed to save group code");
        updated = await resp.json();
        toast.success("Group code updated");
      }
      onSave(updated, mode);
      onHide();
    } catch (e) {
      toast.error(e.message || (mode === "add" ? "Error creating group code" : "Error saving group code"));
    } finally {
      setSaving(false);
    }
  };

  // Split fields for two-column layout: fill left column first, then right, preserving order
  const mid = Math.ceil(GROUP_CODE_FIELDS.length / 2);
  const leftFields = GROUP_CODE_FIELDS.slice(0, mid);
  const rightFields = GROUP_CODE_FIELDS.slice(mid);

  // Determine which required fields are missing/invalid for red border
  const showFieldError = (key) => {
    if (!validationError) return false;
    if (key === "lineCode" && (!form.lineCode || form.lineCode === "")) return true;
    if (key === "line" && (!form.line || form.line === "")) return true;
    if (key === "groupCode" && (!form.groupCode || form.groupCode === "")) return true;
    if (key === "firstNumber") {
      const v = Number(form.firstNumber);
      return (
        form.firstNumber === undefined || form.firstNumber === "" || isNaN(v) || v < 1 || v > 9999 || (form.lastNumber !== undefined && form.lastNumber !== "" && v > Number(form.lastNumber))
      );
    }
    if (key === "lastNumber") {
      const v = Number(form.lastNumber);
      return (
        form.lastNumber === undefined || form.lastNumber === "" || isNaN(v) || v < 1 || v > 9999 || (form.firstNumber !== undefined && form.firstNumber !== "" && Number(form.firstNumber) > v)
      );
    }
    return false;
  };

  return (
    <Modal show={show} onHide={onHide} size="lg" centered contentClassName="group-modal-responsive">
      <Modal.Header closeButton>
        <Modal.Title>{mode === "add" ? "Add Group Code" : "Group Code Details"}</Modal.Title>
      </Modal.Header>
      <Modal.Body style={{ maxHeight: '60vh', overflowY: 'auto', minHeight: 200 }}>
        {validationError && (
          <div className="alert alert-danger py-2 mb-3" role="alert" style={{ fontSize: '0.95em' }}>
            {validationError}
          </div>
        )}
        <Form>
          <Row>
            <Col xs={12} lg={6}>
              {leftFields.map(({ key, label }) => (
                <Form.Group key={key} className="mb-3" as={Row}>
                  <Form.Label column sm={4} xs={5} style={{ fontWeight: 500 }}>{label}</Form.Label>
                  <Col sm={8} xs={7}>
                    <Form.Control
                      name={key}
                      value={form[key] ?? ""}
                      onChange={handleChange}
                      disabled={saving || key === "id" || key === "siteId"}
                      readOnly={key === "id" || key === "siteId"}
                      type={typeof form[key] === "number" ? "number" : "text"}
                      tabIndex={key === "siteId" ? -1 : undefined}
                      isInvalid={showFieldError(key)}
                    />
                  </Col>
                </Form.Group>
              ))}
            </Col>
            <Col xs={12} lg={6}>
              {rightFields.map(({ key, label }) => (
                <Form.Group key={key} className="mb-3" as={Row}>
                  <Form.Label column sm={4} xs={5} style={{ fontWeight: 500 }}>{label}</Form.Label>
                  <Col sm={8} xs={7}>
                    <Form.Control
                      name={key}
                      value={form[key] ?? ""}
                      onChange={handleChange}
                      disabled={saving || key === "id" || key === "siteId"}
                      readOnly={key === "id" || key === "siteId"}
                      type={typeof form[key] === "number" ? "number" : "text"}
                      tabIndex={key === "siteId" ? -1 : undefined}
                      isInvalid={showFieldError(key)}
                    />
                  </Col>
                </Form.Group>
              ))}
            </Col>
          </Row>
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="primary" onClick={handleSave} disabled={saving}>
          {saving ? <Spinner size="sm" animation="border" /> : (mode === "add" ? "Create" : "Save")}
        </Button>
        <Button variant="secondary" onClick={onHide} disabled={saving}>Cancel</Button>
      </Modal.Footer>
    </Modal>
  );
}

const GroupCodesScreen = () => {
  const { user, authenticatedFetch } = useAuth();
  const [groups, setGroups] = useState([]);
  const [loading, setLoading] = useState(true);
  const [sort, setSort] = useState({ key: "id", direction: "asc" });
  // Filtering removed
  const [page, setPage] = useState(1);
  const [pageSize, setPageSize] = useState(20);
  const [totalCount, setTotalCount] = useState(0);
  const [showAdd, setShowAdd] = useState(false);

  useEffect(() => {
    setLoading(true);
    const baseUrl = import.meta.env.VITE_API_BASE_URL || "";
    const params = new URLSearchParams();
    params.append("skip", (page - 1) * pageSize);
    params.append("top", pageSize);
    authenticatedFetch(`${baseUrl}/api/GroupCodes?${params.toString()}`)
      .then(async (r) => {
        if (!r.ok) throw new Error("Failed to fetch group codes");
        const total = r.headers.get("x-total-items");
        const data = await r.json();
        setGroups(Array.isArray(data) ? data : (data.data || []));
        setTotalCount(total ? parseInt(total, 10) : (Array.isArray(data) ? data.length : (data.totalCount || (data.data ? data.data.length : 0))));
      })
      .catch((e) => toast.error(e.message || "Error loading group codes"))
      .finally(() => setLoading(false));
  }, [authenticatedFetch, page, pageSize]);

  // No client-side filtering/sorting; server handles it
  const sorted = groups;
  const totalPages = Math.max(1, Math.ceil((totalCount || groups.length) / pageSize));
  const paged = sorted;

  useEffect(() => {
    // Reset to first page if sort or pageSize changes
    setPage(1);
  }, [sort, pageSize]);

  const handleSave = (updated, mode) => {
    if (mode === "add") {
      // Add new group code to the list and refresh
      setGroups((gs) => [updated, ...gs]);
      setTotalCount((c) => c + 1);
    } else {
      setGroups((gs) => gs.map((g) => (g.id === updated.id ? updated : g)));
    }
  };

  if (!user || !(user.roles?.includes("admin"))) {
    return <div className="mt-4 text-danger">Access denied.</div>;
  }

  return (
    <div className="container mt-4">
      <div className="d-flex justify-content-between align-items-center mb-3">
        <h2 className="mb-0">Group Codes</h2>
        <Button variant="success" onClick={() => setShowAdd(true)}>
          + Add Group Code
        </Button>
      </div>

      <GroupCodeDetailsModal
        show={showAdd}
        onHide={() => setShowAdd(false)}
        mode="add"
        onSave={handleSave}
      />

      {loading ? (
        <div className="text-center my-5">
          <Spinner animation="border" />
        </div>
      ) : (
        <>
          <Row className="g-2">
            {paged.map((group) => (
              <Col key={group.id} xs={12} sm={6} md={4} lg={3} xl={2} xxl={2}>
                <GroupCodeCard group={group} onSave={handleSave} />
              </Col>
            ))}
          </Row>
          {groups.length > 0 && (
            <div className="d-flex justify-content-center align-items-center gap-3 mt-3 flex-wrap">
              <Pagination className="mb-0">
                <Pagination.First onClick={() => setPage(1)} disabled={page === 1} />
                <Pagination.Prev onClick={() => setPage(p => Math.max(1, p - 1))} disabled={page === 1} />
                {/* Show first page */}
                <Pagination.Item active={page === 1} onClick={() => setPage(1)}>{1}</Pagination.Item>
                {/* Show ellipsis if needed before current page */}
                {page > 4 && <Pagination.Ellipsis disabled />} 
                {/* Show up to 2 pages before current */}
                {Array.from({length: 2}, (_, i) => page - 2 + i).filter(p => p > 1 && p < page).map(p => (
                  <Pagination.Item key={p} onClick={() => setPage(p)}>{p}</Pagination.Item>
                ))}
                {/* Show current page if not first/last */}
                {page !== 1 && page !== totalPages && (
                  <Pagination.Item active>{page}</Pagination.Item>
                )}
                {/* Show up to 2 pages after current */}
                {Array.from({length: 2}, (_, i) => page + 1 + i).filter(p => p < totalPages && p > page).map(p => (
                  <Pagination.Item key={p} onClick={() => setPage(p)}>{p}</Pagination.Item>
                ))}
                {/* Show ellipsis if needed after current page */}
                {page < totalPages - 3 && <Pagination.Ellipsis disabled />}
                {/* Show last page if more than one */}
                {totalPages > 1 && (
                  <Pagination.Item active={page === totalPages} onClick={() => setPage(totalPages)}>{totalPages}</Pagination.Item>
                )}
                <Pagination.Next onClick={() => setPage(p => Math.min(totalPages, p + 1))} disabled={page === totalPages} />
                <Pagination.Last onClick={() => setPage(totalPages)} disabled={page === totalPages} />
              </Pagination>
              <Form.Group controlId="pageSizeSelect" className="mb-0">
                <Form.Label visuallyHidden>Items per page</Form.Label>
                <Form.Select size="sm" value={pageSize} onChange={e => setPageSize(Number(e.target.value))}>
                  {[10, 20, 30, 50, 100].map(size => (
                    <option key={size} value={size}>{size} per page</option>
                  ))}
                </Form.Select>
              </Form.Group>
            </div>
          )}
        </>
      )}
      {sorted.length === 0 && !loading && <div>No group codes found.</div>}
    </div>
  );
};

export default GroupCodesScreen;
